.button_button__3Pz0r{
    place-self: center;
}

.button_button__3Pz0r nav ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.button_button__3Pz0r nav ul li {
    cursor: pointer;
    --c: #00AFFA;
    color: var(--c);
    font-size: 25px;
    border-radius: 0.5em;
    width: 10em;
    height: 3em;
    text-transform: uppercase;
    font-weight: bold;
    font-family: sans-serif;
    letter-spacing: 0.1em;
    text-align: center;
    line-height: 3em;
    position: relative;
    overflow: hidden;
    z-index: 1;
    transition: 0.5s;
    margin: 1em;
}

.button_button__3Pz0r nav ul li span {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: var(--c);
    -webkit-transform: translateY(150%);
            transform: translateY(150%);
    border-radius: 50%;
    left: calc((var(--n) - 1) * 25%);
    transition: 0.5s;
    transition-delay: calc((var(--n) - 1) * 0.3s);
    z-index: -1;
}

.button_button__3Pz0r nav ul li:hover {
    color: rgb(255, 255, 255);
}

.button_button__3Pz0r nav ul li:hover span {
    -webkit-transform: translateY(0) scale(1.5);
            transform: translateY(0) scale(1.5);
}

.button_button__3Pz0r nav ul li span:nth-child(1) {
    --n: 1;
}

.button_button__3Pz0r nav ul li span:nth-child(2) {
    --n: 2;
}

.button_button__3Pz0r nav ul li span:nth-child(3) {
    --n: 3;
}

.button_button__3Pz0r nav ul li span:nth-child(4) {
    --n: 4;
}
