@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap");
.home {
    display: grid;
    grid-template-rows: 25vh 75vh;
    height: 100vh;
    width: 100vw;
}

.bannerHome {
    background-color: #00affa;
    color: white;
}

.bannerHome p {
    text-align: center;
}

#title {
    font-weight: bolder;
    font-size: 30px;
    margin: 20px;
}

#subtitle {
    font-weight: bold;
    font-size: 16px;
}

.text {
    text-align: center;
}

.contentHome {
    display: grid;
    grid-template-rows: auto auto auto;
}

.formula {
    display: grid;
    grid-template-rows: auto auto auto;
    justify-items: center;
    align-items: center;
    place-content: center;
}

.formula img {
    width: 30%;
}

#inputs {
    display: grid;
    grid-template-columns: auto auto auto;
    justify-items: center;
    align-items: center;
    place-content: center;
    column-gap: 5%;
}

.sustitucion span {
    font-family: "Roboto Mono", monospace;
    padding-left: 5px;
}

@media (max-width: 500px) {
    #title {
        font-size: 20px;
        margin: 10px;
    }

    #subtitle {
        font-weight: bold;
        font-size: 12px;
    }

    #textHome {
        padding-right: 15%;
        padding-left: 15%;
    }

    .formula {
        padding-left: 5%;
        padding-right: 5%;
    }

    #inputs {
        grid-template-columns: auto;
        grid-template-rows: auto auto auto;
    }
}

/************ RESULTADOS *******************/

.resultados {
    display: grid;
    grid-template-rows: 15vh 85vh;
    height: 100vh;
    width: 100vw;
}

.bannerResultados {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #00affa;
    color: white;
    text-align: center;
}

.contentResultados {
    display: grid;
    grid-template-columns: 40% 60%;
}

.leftPanel {
    display: grid;
    grid-template-rows: auto auto;
    place-content: center;
}

.datacontainer {
    display: grid;
    grid-template-rows: 20% 50%;
    border-radius: 20px;
    background-color: #00affa;
    color: white;
    row-gap: 10%;
}

#description {
    margin-left: 10%;
}

.data {
    display: grid;
    grid-template-rows: auto auto auto;
    justify-items: start;
    align-items: center;
    justify-content: center;
    align-content: center;
}

.error {
    color: rgb(255, 0, 0);
    font-weight: bold;
}

.resultado span {
    font-family: "Roboto Mono", monospace;
    padding-left: 5px;
}

@media (max-width: 900px) {
    .resultados {
        display: grid;
        grid-template-rows: 10vh 90vh;
        height: 100vh;
        width: 100vw;
    }

    .bannerResultados {
        display: grid;
        grid-template-columns: auto auto auto;
        align-items: center;
        justify-items: start;
        justify-content: start;
    }

    .responsivebanner {
        display: grid;
        grid-template-columns: 10vw 80vw 10vw;
        align-items: center;
    }

    .responsivebanner p {
        justify-self: center;
        align-self: center;
        margin-left: 50px;
    }

    .responsivebanner img {
        width: 50px;
        justify-self: center;
        margin-left: 30px;
        cursor: pointer;
    }

    /* Tiene left y right panel */
    .contentResultados {
        display: grid;
        grid-template-columns: auto;
        grid-template-rows: auto auto;
    }

    .leftPanel {
        display: grid;
        grid-template-rows: auto;
        place-content: center;
        margin-top: 25px;
    }

    .datacontainer {
        display: grid;
        grid-template-rows: 20% 50%;
        border-radius: 20px;
        background-color: #00affa;
        color: white;
        row-gap: 10%;
        padding-left: 50px;
        padding-right: 50px;
    }

    .rigthPanel {
        margin-left: 25px;
        margin-right: 25px;
    }
}
